"use clien"
import React, { useState } from "react";
import {
  Card,
  CardMedia,
  CardContent,
  Typography,
  Grid,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  Box,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
} from "@mui/material";

const properties = [
  {
    id: 1,
    title: "Diamond Manor Apartment",
    type: "For Rent",
    address: "145 Willow St #2, Cambridge, MA 02141",
    beds: 2,
    baths: 2,
    sqft: 0,
    dateAvailable: "12/10/2025",
    price: 3000,
    homeType: "Apartment",
    image: "https://via.placeholder.com/300x200",
  },
  {
    id: 2,
    title: "Villa Bay",
    type: "For Sale",
    address: "145 Willow St #2, Cambridge, MA 02141",
    beds: 2,
    baths: 2,
    sqft: 150,
    price: 500000,
    homeType: "Condo",
    image: "https://via.placeholder.com/300x200",
  },
];

const HomeCards = () => {
  const [filters, setFilters] = useState({
    type: "All",
    price: "All",
    beds: "All",
    baths: "All",
    homeType: "All",
  });

  const [isFilterDialogOpen, setFilterDialogOpen] = useState(false);

  const handleFilterChange = (field) => (event) => {
    setFilters({ ...filters, [field]: event.target.value });
  };

  const filteredProperties = properties.filter((property) => {
    return (
      (filters.type === "All" || property.type === filters.type) &&
      (filters.price === "All" ||
        (filters.price === "<3000" && property.price < 3000) ||
        (filters.price === "3000-5000" &&
          property.price >= 3000 &&
          property.price <= 5000) ||
        (filters.price === ">5000" && property.price > 5000)) &&
      (filters.beds === "All" || property.beds === parseInt(filters.beds)) &&
      (filters.baths === "All" || property.baths === parseInt(filters.baths)) &&
      (filters.homeType === "All" || property.homeType === filters.homeType)
    );
  });

  return (
    <Box sx={{ padding: 3 }}>
      {/* Filters Row */}
      <Box sx={{ display: "flex", gap: 2, mb: 3 }}>
        <FormControl variant="outlined" sx={{ minWidth: 150 }}>
          <InputLabel>For Sale/For Rent</InputLabel>
          <Select
            value={filters.type}
            onChange={handleFilterChange("type")}
            label="For Sale/For Rent"
          >
            <MenuItem value="All">All</MenuItem>
            <MenuItem value="For Sale">For Sale</MenuItem>
            <MenuItem value="For Rent">For Rent</MenuItem>
          </Select>
        </FormControl>

        <FormControl variant="outlined" sx={{ minWidth: 150 }}>
          <InputLabel>Price</InputLabel>
          <Select
            value={filters.price}
            onChange={handleFilterChange("price")}
            label="Price"
          >
            <MenuItem value="All">All</MenuItem>
            <MenuItem value="<3000">Less than $3000</MenuItem>
            <MenuItem value="3000-5000">$3000 - $5000</MenuItem>
            <MenuItem value=">5000">More than $5000</MenuItem>
          </Select>
        </FormControl>

        <FormControl variant="outlined" sx={{ minWidth: 150 }}>
          <InputLabel>Beds</InputLabel>
          <Select
            value={filters.beds}
            onChange={handleFilterChange("beds")}
            label="Beds"
          >
            <MenuItem value="All">All</MenuItem>
            <MenuItem value="1">1 Bed</MenuItem>
            <MenuItem value="2">2 Beds</MenuItem>
            <MenuItem value="3">3 Beds</MenuItem>
          </Select>
        </FormControl>

        <FormControl variant="outlined" sx={{ minWidth: 150 }}>
          <InputLabel>Baths</InputLabel>
          <Select
            value={filters.baths}
            onChange={handleFilterChange("baths")}
            label="Baths"
          >
            <MenuItem value="All">All</MenuItem>
            <MenuItem value="1">1 Bath</MenuItem>
            <MenuItem value="2">2 Baths</MenuItem>
            <MenuItem value="3">3 Baths</MenuItem>
          </Select>
        </FormControl>

        <FormControl variant="outlined" sx={{ minWidth: 150 }}>
          <InputLabel>Home Type</InputLabel>
          <Select
            value={filters.homeType}
            onChange={handleFilterChange("homeType")}
            label="Home Type"
          >
            <MenuItem value="All">All</MenuItem>
            <MenuItem value="Apartment">Apartment</MenuItem>
            <MenuItem value="Condo">Condo</MenuItem>
            <MenuItem value="House">House</MenuItem>
          </Select>
        </FormControl>

        <Button
          variant="outlined"
          onClick={() => setFilterDialogOpen(true)}
          sx={{ whiteSpace: "nowrap" }}
        >
          All Filters
        </Button>
      </Box>

      {/* Cards Grid */}
      <Grid container spacing={3}>
        {filteredProperties.map((property) => (
          <Grid item xs={12} sm={6} md={4} key={property.id}>
            <Card>
              <CardMedia
                component="img"
                height="140"
                image={property.image}
                alt={property.title}
              />
              <CardContent>
                <Typography variant="h6">{property.title}</Typography>
                <Typography variant="body2" color="textSecondary">
                  {property.address}
                </Typography>
                <Typography variant="body2">
                  Beds: {property.beds} | Baths: {property.baths} | Price: $
                  {property.price}
                </Typography>
                {property.type === "For Rent" && (
                  <Typography variant="body2" color="textSecondary">
                    Available: {property.dateAvailable}
                  </Typography>
                )}
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>

      {/* All Filters Dialog */}
      <Dialog
        open={isFilterDialogOpen}
        onClose={() => setFilterDialogOpen(false)}
      >
        <DialogTitle>All Filters</DialogTitle>
        <DialogContent>
          <TextField
            label="Search by Address"
            variant="outlined"
            fullWidth
            sx={{ mb: 2 }}
          />
          {/* Additional advanced filters can go here */}
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setFilterDialogOpen(false)}>Close</Button>
          <Button variant="contained" onClick={() => setFilterDialogOpen(false)}>
            Apply
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default HomeCards;
